




















































































































.item_image, .cover_image
    img
        width: 100%
.item_image
    margin-bottom: 12px
.cover_image
    transition: 0.3s
    &:hover
        box-shadow: 8px 8px 8px rgba(0,0,0,0.1)
        transform: translate(-6px, -4px)
body.show-nav
    .productItem
        padding-top: 90px
.productItem
    padding-top: 90px
    max-width: 1360px
    @media screen and (max-width : 767px)
        padding-left: 0 !important
        padding-right: 0 !important
    .productItem-block
        @media screen and (max-width : 767px)
            padding: 0 20px
    h4
        font-size: 15px
        font-weight: 500
        color: rgba(0,0,0,0.9)
        letter-spacing: 0.4px
        margin-bottom: 16px
        @media screen and (max-width : 767px)
            margin-bottom: 24px
    .desc_en
        font-size: 13px
        line-height: 24px
        color: rgba(0,0,0,0.9)
    .desc_zh
        font-size: 12px
        line-height: 24px
        color: rgba(0,0,0,0.9)
        letter-spacing: 1px
    p
        line-height: 1.3rem
    .detail_en
        margin-top: 50px
        p
            font-size: 10px
            color: rgba(0,0,0,0.9)
        @media screen and (max-width : 767px)
            margin-top: 30px
    .detail_zh
        @media screen and (max-width : 767px)
            margin-top: 20px
            margin-bottom: 40px
        p
            font-size: 10px
            color: rgba(0,0,0,0.9)
            letter-spacing: 0.5px
    hr
        border-top: 1px solid #dcdcdc
        @media screen and (max-width : 767px)
            margin: 0 15px
.cover_image_wrapper
    margin-top: 58px
    
    @media screen and (max-width: 767px)
        margin-top: 48px
        padding: 0 15px
    .cover_image_block
        width: 19%
        @media screen and (max-width: 767px)
            width: 49%
