




































































































h6
    a
        font-size: 0.9rem
        color: rgba(0,0,0,0.9)
        &:hover
            text-decoration: none
li
    list-style: none
    a
        font-size: 16px
        color: rgba(0,0,0,0.9)
        letter-spacing: 0.5px
        @media screen and (max-width: 767px)
            font-size: 20px
        &:hover
            text-decoration: none
.header
    // position: fixed
    position: relative
    top: 0
    width: 100%
    z-index: 10
    background-color: #fefefe
    @media screen and (max-width: 767px)
        display: flex
        position: static
        width: 100vw
        background-color: #fefefe
        &.active
            z-index: 10
            position: fixed
            top: 0
            .menubar
                display: flex
                top: 0
                width: 100%
                ul
                    top: 0
                    display: block
    .burger-for-slide-block
        position: fixed
        width: 100%
        z-index: 10
        padding: 20px
        top: -100%
        left: 0
        transition: top .8s
        background-color: #fcfcfc
    .hamburger
        position: absolute
        right: 5px
        top: 5px
        transform: scale(.7)
        padding: 10px 15px
        .hamburger-box
            width: 30px
            .hamburger-inner, .hamburger-inner::before, .hamburger-inner::after
                height: 1px
                width: 30px
    .hamburger.burger-for-slide
        right: 5px
        top: 5px
    .menubar
        ul
            li
                &:last-child
                    a
                        width: 16px
                        display: block
        @media screen and (max-width: 767px)
            ul
                height: calc(100vh - 84px)
                margin-top: 37%
                top: 100vh
                width: 100%
                position: relative
                transition: top 0.5s
                display: none
                li
                    height: 15%
                    &:last-child
                        hr
                            position: absolute
                            left: 0
                            right: 0
                            margin: auto
                            bottom: 45%
                        a
                            position: absolute
                            left: 0
                            right: 0
                            margin: auto
                            bottom: 28%
                            width: 24px
                            svg
                                transform: scale(1.5)
button
    &:focus
        outline: 0px auto transparent
body.show-nav
    .burger-for-slide-block
        position: fixed
        top: 0

