



































































































































































































































































body.show-nav
    .projects
        padding-top: 90px
body
    overflow-x: hidden
.projects
    padding-top: 50px
    h6
        font-size: 16px
        color: rgba(0,0,0,0.9)
        font-weight: 500
        margin-bottom: 24px
        @media screen and (max-width : 767px)
            margin-bottom: 14px
    p
        font-size: 0.6rem
        color: rgba(0,0,0,0.9)
        @media screen and (max-width : 767px)
            font-size: 12px
            color: rgba(0,0,0,0.75)
            letter-spacing: 0.5px
    .cover_image
        transition: 0.3s
        &:hover
            box-shadow: 12px 12px 12px rgba(0,0,0,0.1)
            transform: translate(-6px, -4px)
        img
            width: 100%
    .title_en
        font-size: 12px
        color: rgba(0,0,0,0.75)
        letter-spacing: 0.5px
        margin-top: 16px
        @media screen and (max-width : 767px)
            margin-top: 12px
    .product_design_desc
        color: rgba(0,0,0,0.5)
        font-size: 14px
        font-weight: 300
        letter-spacing: 0.5px
        margin-bottom: .5rem
        @media screen and (max-width : 767px)
            margin-bottom: 6px
.product_design
    padding-left: 20px !important
    padding-right: 20px !important
    max-width: 1360px
    padding-top: 80px
    padding-bottom: 30px
    @media screen and (max-width : 767px)
        padding-top: 10px
    .product-block
        @media screen and (max-width : 767px)
            margin-bottom: 24px
.web_design
    background-color: #f9f9f9
    padding-bottom: 110px
    @media screen and (max-width : 767px)
        padding-left: 0 !important
        padding-right: 0 !important
        padding-bottom: 50px
    h6
        padding-left: 20px
        font-size: 16px
        color: rgba(0,0,0,0.9)
        margin-bottom: 30px
    .tabpanel
        margin-left: -5px
    .nav-pills
        a
            font-size: 14px
            color: rgba(0,0,0,0.9)
            letter-spacing: 0.5px
        .nav-link
            color: rgba(0,0,0,0.25)
            background-color: transparent
            padding: 4px 16px
            &:first-child
                padding: 0px 16px 4px 16px
            &.active
                color: rgba(0,0,0,0.9)
                background-color: transparent
                font-weight: 500
            &:hover
                color: black
    .tab-content
        position: relative
        .active
            display: flex
        .tab-img-pc
            position: relative
            img
                width: 100% 
                height: auto 
                z-index: 1
        .tab-img-mob
            position: relative
            max-width: 360px
            img
                width: 100% 
                height: auto 
                z-index: 1
        .takeALook
            position: absolute
            right: 0
            top: 110%
            font-size: 14px
            color: rgba(0,0,0,0.9)
            &:hover
                text-decoration: none
            img
                width: 26px
                padding-left: 6px
    .slider-1
        .owl-item
            a 
                display: block
