            <div class="d-flex flex-wrap justify-content-between mb-5 cover_image_wrapper">
                <div class="cover_image_block mb-3"
                v-for="project in project_Other" :key="project.index">
                    <router-link :to="project.access_path">
                        <div class="cover_image">
                            <img :src="project.cover_path" alt="">
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import projectsRawData from '@/data/projects.js'
import productItemsRawData from '@/data/productItems.js'

export default {
    name: 'ProductItem',
    data () {
        return {

        }
    },
    mounted () {
        if (this.productItem_Allpath.indexOf(this.$route.params.project_name) == -1) {
            this.$router.push('/error')
        }
        window.scrollTo(0, 0)
    },
    computed: {
        productItem () {
            return productItemsRawData.filter(item => item.path == this.$route.params.project_name)[0]
        },
        project_Other () {
            return projectsRawData.filter(item => item.path != this.$route.params.project_name)
        },
        productItem_Allpath () {
            return productItemsRawData.map(item => item.path)
        }
    },
    watch: {
        '$route': function (to, from) {
        // console.log(to, from);
        if (to.params.project_name != from.params.project_name) {
            window.scrollTo(0, 0)
        }
        }
    }
}
</script>

