




































































































































































.item_image, .cover_image
    img
        width: 100%
body.show-nav
    .servicePlatformItem
        padding-top: 90px
.servicePlatformItem
    padding-top: 90px
    max-width: 1360px
    @media screen and (max-width : 767px)
        padding-left: 0 !important
        padding-right: 0 !important
    .servicePlatformItem-wrapper
        margin-bottom: 58px
        @media screen and (max-width : 767px)
            margin-bottom: 0
        .servicePlatformItem-block
            @media screen and (max-width : 767px)
                padding-bottom: 36px
                padding-left: 20px !important
                padding-right: 20px !important
    .cover_image_block
        max-width: 31%
        a 
            display: block
            .cover_image
                transition: 0.3s
                // width: 436px
                width: 100%
                height: auto
                overflow: hidden
                border-radius: 8px
                &:hover
                    box-shadow: 8px 8px 8px rgba(0,0,0,0.1)
                    transform: translate(-6px, -4px)
                    @media screen and (max-width: 767px)
                        box-shadow: none
                        transform: translate(0, 0)
    .detail-mobile
        padding-left: 20px
        padding-right: 20px
    h4
        font-size: 15px
        font-weight: 500
        color: rgba(0,0,0,0.9)
        letter-spacing: 0.4px
    .desc_en
        font-size: 13px
        line-height: 24px
        color: rgba(0,0,0,0.9)
    .desc_zh
        font-size: 12px
        line-height: 24px
        color: rgba(0,0,0,0.9)
        letter-spacing: 1px
    p
        line-height: 1.3rem
    .detail_en
        margin-top: 50px
        @media screen and (max-width : 767px)
            margin-top: 30px
        p
            font-size: 10px
            color: rgba(0,0,0,0.9)
    .detail_zh
        @media screen and (max-width : 767px)
            margin-top: 20px
            margin-bottom: 40px
        p
            font-size: 10px
            color: rgba(0,0,0,0.9)
            letter-spacing: 0.5px
    hr
        border-top: 1px solid #dcdcdc
        @media screen and (max-width : 767px)
            margin: 0 15px
    .slider-4
        .owl-item
            a 
                display: block
.video-out-box
    position: relative
    img
        width: 100.2%
        position: absolute
        left: 0
        right: 0
        z-index: 1
        pointer-events: none
    @media screen and (max-width: 767px)
        width: 260px
        height: auto
