



































body.show-nav
    .about
        padding-top: 90px
.about
    height: calc(100vh - 72px)
    @media screen and (max-width : 991px)
        padding-top: 50px
        height: calc(100vh - 54px)
        overflow: hidden
    .about-block-wrapper
        height: calc(100vh - 72px)
        @media screen and (max-width : 991px)
            height: calc(100vh - 104px)
        .about-block
            height: calc(100vh - 72px)
            @media screen and (max-width : 991px)
                height: calc(100vh - 104px)
                padding-left: 20px !important
                padding-right: 20px !important
            img
                width: 90%
                @media screen and (max-width : 991px)
                    position: absolute
                    bottom: -30px
                    left: 40%
p
    letter-spacing: 0.5px
    font-size: 20px
    line-height: 28px
    color: rgba(0,0,0,0.9)
    span
        color: #97a3af
.comment
    @media screen and (max-width : 991px)
        padding-top: 50px
    p
        &:first-child
            font-size: 20px
            line-height: 30px
            color: rgba(0,0,0,0.9)
            margin-bottom: 30px
            @media screen and (max-width: 767px)
                font-size: 14px
                font-weight: 500
                line-height: 26px
        &:nth-child(2)
            font-size: 14px
            color: rgba(0,0,0,0.9)
            letter-spacing: 0.5px
            font-weight: 500
            line-height: 26px
            margin-top: 60px
            span
                font-size: 14px
                color: rgba(0,0,0,0.7)
                letter-spacing: 0.5px
                font-weight: 300
                @media screen and (max-width: 767px)
                    font-size: 12px
                    line-height: 22px
            @media screen and (max-width: 767px)
                font-size: 12px
                position: absolute
                bottom: 124px
                z-index: 1
    .show-1440
        display: none
        @media screen and (min-width: 1440px)
            display: inline-block
