</template>

<script>
import servicePlatformItemsRawData from '@/data/servicePlatformItems.js'
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'

export default {
  name: 'ServicePlatformItem',
  components: {
    videoPlayer
  },
  data () {
    return {

    }
  },
  mounted () {
    this.initCarousel()

    if (this.servicePlatformItem_Allpath.indexOf(this.$route.params.project_name) == -1) {
      this.$router.push('/error')
    }
    window.scrollTo(0, 0)
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    },
    servicePlatformItem () {
      return servicePlatformItemsRawData.filter(item => item.path == this.$route.params.project_name)[0]
    },
    servicePlatformItem_Other () {
      return servicePlatformItemsRawData.filter(item => item.path != this.$route.params.project_name)
    },
    servicePlatformItem_Allpath () {
      return servicePlatformItemsRawData.map(item => item.path)
    }
  },
  methods: {
    initCarousel () {
      var owl = $('.owl-carousel.slider-4')
      owl.owlCarousel({
        loop: false,
        items: 1.5,
        center: true,
        margin: 30,
        nav: false,
        dots: false
      })
      // console.log('slider init, 4');
    }
  },
  watch: {
    '$route': function (to, from) {
      // console.log(to, from);
      if (to.params.project_name != from.params.project_name) {
        window.scrollTo(0, 0)
        this.$nextTick(() => {
          this.initCarousel()
        })
      }
    }
  }
}
</script>

